*{
  padding: 0%;
  margin: 0%;
}


.box{
  height: 250px;
  width: 230px;
  /* border: 2px solid black; */
  border: 1px solid #ec5626;
  border-radius: 4px;
  margin: 20px;
  
}
.box img{
  height: 100px;
  width: 100px;
  margin-left:22%;
  transition: all ease .3s;
}
input{
  visibility: hidden;
}
h4{
  text-align: center;
  margin-top: 20px;
  color: #ec5626;
}

button {background-image: linear-gradient(to right, #C04848 0%, #480048  51%, #C04848  100%)}
button {
   margin: 30px;
   margin-left: 40px;
   padding: 15px 45px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: white;            
   box-shadow: 0 0 20px #eee;
   border-radius: 10px;
   display: block;
 }

 button:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }
 .box img:hover{
   transform: translateY(-20px);
 }
